@charset "UTF-8";
html { color: #333; background: #f3f2ee; -webkit-text-size-adjust: 100%; -ms-text-size-adjust: 100%; text-rendering: optimizelegibility; }

body, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, menu, nav, section { margin: 0; padding: 0; }

article, aside, details, figcaption, figure, footer, header, menu, nav, section { display: block; }

audio, canvas, video { display: inline-block; }

body, button, input, select, textarea { font: 300 1em/1.8 PingFang SC, Lantinghei SC, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans; }

button::-moz-focus-inner, input::-moz-focus-inner { padding: 0; border: 0; }

table { border-collapse: collapse; border-spacing: 0; }

fieldset, img { border: 0; }

blockquote { position: relative; color: #3f3f3f; font-weight: 400; border-left: 10px solid rgba(128, 128, 128, 0.075); padding: 15px 20px; margin: 1em 0; background-color: rgba(128, 128, 128, 0.05); }

blockquote p { margin: 0 !important; }

@media only screen and (max-width: 640px) { blockquote { margin: 1em 0; } }

img { -ms-interpolation-mode: bicubic; }

iframe { display: block; }

acronym, abbr { border-bottom: 1px dotted; font-variant: normal; }

abbr { cursor: help; }

del { text-decoration: line-through; }

address, caption, cite, code, dfn, em, th, var { font-style: normal; font-weight: 400; }

ul, ol { list-style: none; }

caption, th { text-align: left; }

q:before, q:after { content: ''; }

sub, sup { font-size: 75%; line-height: 0; position: relative; }

:root sub, :root sup { vertical-align: baseline; }

sup { top: -0.5em; }

sub { bottom: -0.25em; }

a { color: #1abc9c; }

a:hover { text-decoration: underline; }

ins, a { text-decoration: none; }

u, .typo-u { text-decoration: underline; }

mark { background: #fffdd1; border-bottom: 1px solid #ffedce; padding: 2px; margin: 0 5px; }

pre, code, pre tt { font-family: "Source Code Pro Medium",Consolas,Menlo,Monaco,"Courier New",monospace; overflow-x: auto; vertical-align: middle; font-size: .95em; line-height: 1.5; }

pre { background-color: rgba(128, 128, 128, 0.1); padding: 10px; display: block; -webkit-overflow-scrolling: touch; word-wrap: break-word; overflow: hidden; }

code { margin: 0 2px; padding: 1px 4px; color: #4d4d4d; background-color: rgba(128, 128, 128, 0.1); border-radius: 2px; }

pre code { margin: 0; padding: 0; background: none; border: none; }

hr { border: none; border-bottom: 1px solid #cfcfcf; margin-bottom: 0.8em; height: 10px; }

small, .typo-small, figcaption { font-size: 0.9em; color: #888; }

strong, b { font-weight: bold; color: #000; }

.textwrap, .textwrap td, .textwrap th { word-wrap: break-word; word-break: break-all; }

.textwrap-table { table-layout: fixed; }

::selection { background: rgba(0, 0, 0, 0.1); }

::-moz-selection { background: rgba(0, 0, 0, 0.1); }

img::selection { background: rgba(127, 191, 17, 0.2); width: 100%; }

img::-moz-selection { background: rgba(127, 191, 17, 0.2); width: 100%; }

@font-face { font-family: "FontAwesome"; src: url("font/fontawesome-webfont.eot"); src: url("font/fontawesome-webfont.eot?#iefix") format("eot"), url("font/fontawesome-webfont.woff") format("woff"), url("font/fontawesome-webfont.ttf") format("truetype"), url("../font/fontawesome-webfont.svg#FontAwesome") format("svg"); font-weight: normal; font-style: normal; }

[class^="icon-"]:before, [class*=" icon-"]:before { font-family: FontAwesome; font-weight: normal; font-style: normal; display: inline-block; text-decoration: inherit; font-size: 24px; }

a [class^="icon-"], a [class*=" icon-"] { display: inline-block; text-decoration: inherit; }

nav ul li [class^="icon-"].icon-user:before { content: "\f007"; }

nav ul li [class^="icon-"].icon-home:before { content: "\f015"; }

/* \f020 doesn't work in Safari. all shifted one down */
nav ul li [class^="icon-"].icon-tags:before { content: "\f02c"; }

nav ul li [class^="icon-"].icon-bookmark:before { content: "\f02e"; }

.backTop .icon-circle-arrow-up:before { content: "\f0aa"; }

nav ul li [class^="icon-"].icon-link:before { content: "\f0c1"; }

/* 保证块/段落之间的空白隔行 */
.typo p, .typo pre, .typo ul, .typo ol, .typo dl, .typo form, .typo hr, .typo table, .typo-p, .typo-pre, .typo-ul, .typo-ol, .typo-dl, .typo-form, .typo-hr, .typo-table, blockquote { margin-bottom: 1.2em; }

h1, h2, h3, h4, h5, h6 { font-family: PingFang SC, Verdana, Helvetica Neue, Microsoft Yahei, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans-serif; font-weight: 100; color: #000; line-height: 1.35; }

/* 标题应该更贴紧内容，并与其他块区分，margin 值要相应做优化 */
.typo h1, .typo h2, .typo h3, .typo h4, .typo h5, .typo h6, .typo-h1, .typo-h2, .typo-h3, .typo-h4, .typo-h5, .typo-h6 { margin-top: 1.2em; margin-bottom: 0.6em; line-height: 1.35; }

.typo h1, .typo-h1 { margin: 0.3em 0 0.15em 0; font-size: 2em; }

.typo h2, .typo-h2 { font-size: 1.8em; }

.typo h3, .typo-h3 { font-size: 1.6em; }

.typo h4, .typo-h4 { font-size: 1.4em; }

.typo h5, .typo h6, .typo-h5, .typo-h6 { font-size: 1.2em; }

/* 在文章中，应该还原 ul 和 ol 的样式 */
.typo ul, .typo-ul { margin-left: 1.3em; list-style: disc; }

.typo ol, .typo-ol { list-style: decimal; margin-left: 1.9em; }

.typo li ul, .typo li ol, .typo-ul ul, .typo-ul ol, .typo-ol ul, .typo-ol ol { margin-bottom: 0.8em; margin-left: 2em; }

.typo li ul, .typo-ul ul, .typo-ol ul { list-style: circle; }

/* 同 ul/ol，在文章中应用 table 基本格式 */
.typo table th, .typo table td, .typo-table th, .typo-table td, .typo table caption { border: 1px solid #ddd; padding: 0.5em 1em; color: #666; }

.typo table th, .typo-table th { background: #fbfbfb; }

.typo table thead th, .typo-table thead th { background: #f1f1f1; }

.typo table caption { border-bottom: none; }

/* 去除 webkit 中 input 和 textarea 的默认样式  */
.typo-input, .typo-textarea { -webkit-appearance: none; border-radius: 0; }

.typo-em, .typo em, legend, caption { color: #000; font-weight: inherit; }

/* 着重号，只能在少量（少于100个字符）且全是全角字符的情况下使用 */
.typo-em { position: relative; }

.typo-em:after { position: absolute; top: 0.65em; left: 0; width: 100%; overflow: hidden; white-space: nowrap; content: "・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・・"; }

/* Responsive images */
.typo img { max-width: 100%; }

.fl { float: left; }

.fr { float: right; }

.clearfix { *zoom: 1; }

.clearfix:before, .clearfix:after { content: ""; display: table; }

.clearfix:after { clear: both; overflow: hidden; }

body { padding: 1.5em 20.5% 0 11.5%; }

@media screen and (max-width: 1023px) { body { padding: 1.5em 1.5% 0 11.5%; } }

@media screen and (max-width: 640px) { body { padding: 5em 0.5em 0 0.5em; font-size: .85em; } }

h2, h3 { color: #1abc9c; }

.lotus-article { overflow: hidden; *zoom: 1; min-height: 600px; }

.lotus-article .lotus-post { margin-top: 2em; word-break: break-all; }

.block-category, .block-catalog { position: absolute; top: 1.5em; right: 1.5%; background: #fff; width: 18%; }

.block-category .conter, .block-catalog .conter { padding: 1.5em 2em; }

.block-category h3, .block-catalog h3 { padding-bottom: 0.5em; }

@media screen and (max-width: 1023px) { .block-category, .block-catalog { position: static; margin: 1em 0 0 0; width: auto; } }

.lotus ol { list-style: decimal; margin-left: 2em; }

.lotus li ol, .lotus li ul { margin: 0 0 0 2em; list-style: circle; }

.lotus .meta { font-size: 12px; color: #a8a8a8; }

.lotus .anno { text-align: right; margin-top: 5em; padding: 5px 0; font-size: 12px; color: #A0A0A0; border-top: 1px solid #d0d0d0; }

.lotus .anno a { color: #1F0909; }

.lotus .pageturn { margin-top: 1em; }

.lotus .pageturn .prev { float: left; }

.lotus .pageturn .next { float: right; }

nav { background: -webkit-linear-gradient(180deg, #292929 0%, #404040 8%, #474747 14%, #474747 100%); background: -moz-linear-gradient(180deg, #292929 0%, #404040 8%, #474747 14%, #474747 100%); background: -ms-linear-gradient(180deg, #292929 0%, #404040 8%, #474747 14%, #474747 100%); background: linear-gradient(180deg, #292929 0%, #404040 8%, #474747 14%, #474747 100%); position: fixed; left: 0; top: 0; width: 10%; height: 100%; background: #474747; border-right: 1px solid #212121; z-index: 5; }

nav .logo { padding: 2.5em 0; text-align: center; color: #fff; font-size: 24px; }

nav .logo img { -webkit-border-radius: 50%; -moz-border-radius: 50%; -ms-border-radius: 50%; border-radius: 50%; width: 40%; min-width: 40px; }

@media screen and (max-width: 1023px) { nav .logo p { display: none; } }

nav ul { border-bottom: 1px solid #313131; }

nav ul li { height: 54px; display: block; line-height: 54px; text-align: center; border-top: 1px solid #313131; overflow: hidden; }

nav ul li a { display: block; padding: 0 5px; color: #fff; cursor: pointer; }

nav ul li a span { display: inline-block; margin-left: 1em; width: 3em; text-align: left; }

nav ul li.current a, nav ul li a:hover { -webkit-box-shadow: 1px 1px 24px rgba(0, 0, 0, 0.8) inset; -moz-box-shadow: 1px 1px 24px rgba(0, 0, 0, 0.8) inset; -ms-box-shadow: 1px 1px 24px rgba(0, 0, 0, 0.8) inset; box-shadow: 1px 1px 24px rgba(0, 0, 0, 0.8) inset; -webkit-transition: box-shadow 0.3s linear; -moz-transition: box-shadow 0.3s linear; -ms-transition: box-shadow 0.3s linear; transition: box-shadow 0.3s linear; text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); text-decoration: none; }

nav ul li [class^="icon-"] { display: inline-block; vertical-align: -3px; width: 1.25em; text-align: center; }

@media screen and (max-width: 640px) { nav { width: 100%; height: auto; text-align: center; }
  nav .logo { display: none; }
  nav ul li { display: inline-block; vertical-align: top; min-width: 15%; border-top: none; }
  nav ul li span { display: none !important; } }

.rpost { margin-bottom: 1.2em; padding: 2em 3em; background-color: #fff; }

@media screen and (max-width: 640px) { .rpost { padding: 1em 1em !important; } }

.breadcrub { font-size: 14px; color: #898989; }

.page-home .rpost { padding: 1.5em 3em; }

.page-home .rpost h2 { margin: 5px 0; font-weight: normal; }

.page-post .meta { padding-bottom: 1em; border-bottom: 3px double #eee; }

.page-post table th, .page-post table td { border: 1px solid #d0d0d0; padding: 5px 10px; }

.page-post table th { background: rgba(214, 214, 214, 0.5); }

.page-post table thead th { background: rgba(214, 214, 214, 0.8); }

.page-archives .tit-year { font-size: 1.5em; }

.page-category .block-tag { margin-bottom: 2em; }

.page-category .block-tag p { overflow: hidden; *zoom: 1; }

.page-category .block-tag p a { float: left; margin: 5px 10px 0 0; padding: 3px 10px; border-radius: 2px; font-size: 14px; background-color: #ddd; color: #555; }

.page-category .block-tag p a em { font-size: 12px; position: relative; top: -5px; left: 2px; color: #cc0000; }

.page-category .block-tag p a:hover { background: #eaeaea; text-decoration: none; }

.block-category ul { margin-left: 2px; }

.block-catalog { max-height: 81%; overflow-y: auto; }

.block-catalog h3 { margin-bottom: 0.5em; font-size: 1.2em; }

.block-catalog li { margin-left: 1.3em; padding: 3px 0; list-style-type: disc; line-height: 1.5; font-size: 14px; }

.block-catalog li ul { margin-left: 0; }

.block-catalog li ul li { list-style: circle; }

.block-catalog li a { color: #008E59; }

.block-catalog li.active { background: #f6f6f6; }

.block-catalog li.active a { color: #333; }

.block-catalog.fixed { position: fixed; top: 0; }

.homePage { text-align: center; }

.homePage a, .homePage span { display: inline-block; *vertical-align: auto; *display: inline; *zoom: 1; -webkit-border-radius: 3px; -moz-border-radius: 3px; -ms-border-radius: 3px; border-radius: 3px; margin: 0 5px; padding: 0 12px; height: 32px; line-height: 32px; background: #fff; color: #555; }

.homePage .active { background: none; }

.backTop { margin-right: 1em; color: #474747; opacity: 0.5; }

.backTop:hover { opacity: .8; text-decoration: none; }

.backTop i:before { font-size: 72px; }

.tree { font-size: 14px; list-style-type: square; }

.tree li { list-style-type: disc; font-family: "Open Sans","Helvetica Neue",Helvetica,Arial,STHeiti,"Microsoft Yahei",sans-serif; }

.tree li.sub { margin-left: 3.3em; list-style-type: circle; }

@media print { .lotus-nav { display: none; } }

@media screen { .str { color: #080; }
  .kwd { color: #004488; }
  .com { color: #ABABAB; }
  .typ { color: #990099; }
  .lit { color: #669900; }
  .pun, .opn, .clo { color: #660; }
  .tag { color: #DA573A; }
  .atn { color: #606; }
  .atv { color: #080; }
  .dec, .var { color: #606; }
  .fun { color: red; } }

.line-numbers { display: none; }

#comments { margin: 2em 0 0 0; }

@media screen and (max-width: 1023px) { #comments { margin: 2em 2% 0 2%; } }

footer { padding: 0.5em 0; clear: both; margin-top: 3em; line-height: 1.5; }
